@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Spartan', sans-serif;
  background-color: hsl(180, 52%, 96%);
  font-size: 15px;
}

main {
  min-height: 100vh;
  position: relative;
}

header {
  background-color: hsl(180, 29%, 50%);
  position: relative;
  z-index: 1;
}

.bg-desktop {
  width: 100%;
}

.filter-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  z-index: 100;
  margin: -40px auto -20px auto;
  padding: 30px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 10px 15px -7px rgb(93 164 164 / 50%);
  width: 85%;
}

.hide {
  display: none;
}

.filter-items-container {
  display: inline-block;
  height: 100%;
}

.filter-item-container {
  display: inline-block;
  margin-right: 15px;
}

.filter-item,
.remove-item {
  font-weight: 700;
  padding: 12px 14px;
}

.filter-item {
  background-color: hsl(180, 31%, 95%);
  color: hsl(180, 29%, 50%);
}

.remove-item {
  background-color: hsl(180, 29%, 50%);
  color: white;
}

.remove-item:hover {
  cursor: pointer;
  background-color: hsl(180, 14%, 20%);
}

.clear-items {
  margin-right: 15px;
  background-color: inherit;
  font-size: 16px;
  height: 16px;
  color: hsl(180, 29%, 50%);
}

.clear-items:hover {
  cursor: pointer;
  border-bottom: solid 2px hsl(180, 29%, 50%);
}

@media screen and (max-width: 1200px) {
  .filter-container {
    width: 90%;
  }
}

@media screen and (max-width: 1050px) {
  .filter-container {
    width: 95%;
    padding: 25px;
  }
}

@media screen and (max-width: 965px) {
  .filter-container {
    width: 70%;
    padding: 25px;
  }

  .filter-item-container {
    margin: 5px 5px 25px 5px;
  }

  .filter-item,
  .remove-item {
    font-weight: 700;
    padding: 4px 6px;
    font-size: 13px;
  }

  .clear-items {
    margin-right: 15px;
    background-color: inherit;
    font-size: 14px;
    height: 14px;
    color: hsl(180, 29%, 50%);
  }
}

@media screen and (max-width: 575px) {
  .filter-container {
    width: 95%;
    padding: 25px;
  }
}

.job-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 50px auto;
  padding: 25px 30px;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0px 10px 15px -7px rgb(93 164 164 / 50%);
  border-left: 6px solid hsl(180, 29%, 50%);
  width: 85%;
}

.job-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1 1;
}

.job-description {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-left: 20px;
}

.company-name {
  color: hsl(180, 29%, 50%);
  font-weight: 700;
}

.new {
  margin: 0 5px 0px 8px;
  background-color: hsl(180, 29%, 50%);
}

.featured {
  background-color: hsl(180, 14%, 20%);
}

.new,
.featured {
  color: white;
  border-radius: 14px;
  font-size: 10px;
  padding: 6px 10px;
}

h1 {
  font-size: 20px;
  margin: 10px 0px;
  font-weight: 700;
}

.dot {
  font-size: 20px;
  margin: 0 3px;
}

.date-posted,
.dot,
.job-type,
.job-location {
  font-weight: 500;
  opacity: 0.45;
}

.role,
.level,
.language,
.tool {
  margin: 0 7px;
  background-color: hsl(180, 31%, 95%);
  color: hsl(180, 29%, 50%);
  padding: 12px 14px;
  font-weight: 700;
}

.role:hover,
.level:hover,
.language:hover,
.tool:hover {
  cursor: pointer;
}

@media screen and (max-width: 1200px) {
  .job-container {
    width: 90%;
  }
}

@media screen and (max-width: 1050px) {
  .job-container {
    width: 95%;
    padding: 25px;
  }
}

@media screen and (max-width: 965px) {
  .job-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 70%;
    padding: 30px 10px;
  }

  .job-heading {
    margin: 12px 0 30px 0;
    padding-bottom: 8px;
    border-bottom: 1px solid hsl(180, 8%, 52%);
    width: 100%;
    flex: none;
  }

  .job-description {
    margin: 0px;
  }

  .company-logo {
    position: relative;
  }

  .logo {
    position: absolute;
    top: -55px;
    left: 6px;
    width: 55px;
    height: 55px;
  }

  .heading-info {
    margin: 5px 0;
  }

  .company-name {
    font-size: 14px;
  }

  .new,
  .featured {
    color: white;
    border-radius: 12px;
    font-size: 10px;
    padding: 4px 8px;
  }

  h1 {
    font-size: 17px;
    margin: 15px 0px;
    font-weight: 700;
  }

  .date-posted,
  .job-type,
  .job-location {
    font-size: 13px;
  }

  .skills {
    display: flex;
    flex-wrap: wrap;
  }

  .role,
  .level,
  .language,
  .tool {
    margin-bottom: 10px;
    padding: 8px 10px;
    font-size: 13px;
  }
}

@media screen and (max-width: 575px) {
  .job-container {
    width: 95%;
    padding: 25px;
    margin-top: 65px;
  }
}


